"use client"

import { useParams } from "next/navigation"
import SkillExam from "@/components/SkillExam"

type SkillType = "reading" | "listening" | "writing" | "speaking"

export default function SkillExamPage() {
  const params = useParams()
  const skill = params?.skill as string

  if (!["reading", "listening", "writing", "speaking"].includes(skill)) {
    return <div>Invalid skill type</div>
  }
  
  return (
    <div className="min-h-screen">
      <SkillExam skill={skill as SkillType} />
    </div>
  )
}
