"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Textarea } from "@/components/ui/textarea"
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group"
import { Label } from "@/components/ui/label"
import { Card } from "@/components/ui/card"

export type QuestionType = "multiple-choice" | "writing" | "listening" | "speaking"

interface Option {
  id: string
  text: string
}

interface QuestionProps {
  id: string
  type: QuestionType
  question: string
  passage?: string
  options?: Option[]
  hint?: string
  isPracticeMode?: boolean
  onAnswer: (answer: string) => void
}

export default function ExamQuestion({
  id,
  type,
  question,
  passage,
  options = [],
  hint,
  isPracticeMode = false,
  onAnswer,
}: QuestionProps) {
  const [answer, setAnswer] = useState("")
  const [showHint, setShowHint] = useState(false)

  const handleSubmit = () => {
    if (answer) {
      onAnswer(answer)
    }
  }

  const renderQuestionContent = () => {
    switch (type) {
      case "multiple-choice":
        return (
          <div className="space-y-4 p-4">
            {options.map((option) => (
              <div
                key={option.id}
                className={`flex items-center gap-4 p-4 rounded-lg border-2 cursor-pointer transition-all ${
                  answer === option.id 
                    ? 'border-black bg-gray-50' 
                    : 'border-gray-200 hover:border-gray-300'
                }`}
                onClick={() => setAnswer(option.id)}
              >
                <div className="flex items-center justify-center w-5 h-5">
                  <div className={`w-5 h-5 rounded-full border-2 ${
                    answer === option.id 
                      ? 'border-black bg-black' 
                      : 'border-gray-300'
                  }`}>
                    {answer === option.id && (
                      <div className="w-2 h-2 bg-white rounded-full m-auto mt-1" />
                    )}
                  </div>
                </div>
                <span className="text-base">{option.text}</span>
              </div>
            ))}
          </div>
        )

      case "writing":
        return (
          <Textarea
            value={answer}
            onChange={(e) => setAnswer(e.target.value)}
            placeholder="Type your answer here..."
            className="min-h-[200px]"
          />
        )

      case "listening":
        return (
          <div className="space-y-4">
            <div className="bg-gray-100 p-4 rounded-lg">
              <p className="text-sm text-gray-600">
                Listen to the audio and answer the question
              </p>
            </div>
            <Textarea
              value={answer}
              onChange={(e) => setAnswer(e.target.value)}
              placeholder="Type your answer here..."
              className="min-h-[100px]"
            />
          </div>
        )

      case "speaking":
        return (
          <div className="space-y-4">
            <div className="bg-gray-100 p-4 rounded-lg">
              <p className="text-sm text-gray-600">
                Record your response to the question
              </p>
            </div>
            <Button
              onClick={() => setAnswer("recording")}
              variant="outline"
              className="w-full"
            >
              Start Recording
            </Button>
          </div>
        )

      default:
        return null
    }
  }

  return (
    <Card className="p-6 space-y-6">
      {passage && (
        <div className="space-y-4 mb-8">
          <h3 className="text-lg font-semibold">Reading Passage</h3>
          <div className="bg-gray-50 p-4 rounded-lg">
            <p className="text-gray-700 leading-relaxed">{passage}</p>
          </div>
        </div>
      )}
      <div className="space-y-4">
        <h3 className="text-lg font-semibold">Question {id}</h3>
        <p className="text-gray-700">{question}</p>
      </div>

      <div className="space-y-4">
        {renderQuestionContent()}
      </div>

      {isPracticeMode && hint && (
        <div className="mt-4">
          <Button
            variant="outline"
            onClick={() => setShowHint(!showHint)}
            className="text-sm"
          >
            {showHint ? "Hide Hint" : "Show Hint"}
          </Button>
          {showHint && (
            <p className="mt-2 text-sm text-gray-600 bg-gray-50 p-3 rounded">
              {hint}
            </p>
          )}
        </div>
      )}

      <div className="flex justify-end space-x-3 mt-6">
        <Button
          variant="default"
          onClick={handleSubmit}
          disabled={!answer}
        >
          Submit Answer
        </Button>
      </div>
    </Card>
  )
}
