"use client"

interface ExamHeaderProps {
  title: string;
  imageUrl?: string;
  subtitle?: string;
}

export default function ExamHeader({ title, imageUrl, subtitle }: ExamHeaderProps) {
  return (
    <div className="relative w-full mb-8 rounded-lg overflow-hidden">
      <div className={`relative py-8 flex flex-col items-center justify-center text-center px-4 ${imageUrl ? 'h-48' : ''}`}>
        {imageUrl ? (
          <>
            <div
              className="absolute inset-0 bg-cover bg-center"
              style={{
                backgroundImage: `url(${imageUrl})`,
              }}
            >
              <div className="absolute inset-0 bg-black/50" />
            </div>
            <h1 className="relative text-3xl md:text-4xl font-bold text-white mb-2">
              {title}
            </h1>
            {subtitle && (
              <p className="relative text-lg text-gray-200">
                {subtitle}
              </p>
            )}
          </>
        ) : (
          <>
            <h1 className="text-3xl md:text-4xl font-bold text-gray-900 mb-2">
              {title}
            </h1>
            {subtitle && (
              <p className="text-lg text-gray-600">
                {subtitle}
              </p>
            )}
          </>
        )}
      </div>
    </div>
  )
}
