"use client"

import { useEffect, useState } from "react"
import { Progress } from "@/components/ui/progress"

interface ExamTimerProps {
  duration: number // Duration in seconds
  onTimeUp?: () => void
}

export default function ExamTimer({ duration, onTimeUp }: ExamTimerProps) {
  const [timeLeft, setTimeLeft] = useState(duration)
  const [progress, setProgress] = useState(100)

  useEffect(() => {
    if (timeLeft <= 0) {
      onTimeUp?.()
      return
    }

    const timer = setInterval(() => {
      setTimeLeft((prev) => {
        const newTime = prev - 1
        setProgress((newTime / duration) * 100)
        return newTime
      })
    }, 1000)

    return () => clearInterval(timer)
  }, [timeLeft, duration, onTimeUp])

  const formatTime = (seconds: number) => {
    const hours = Math.floor(seconds / 3600)
    const minutes = Math.floor((seconds % 3600) / 60)
    const remainingSeconds = seconds % 60

    return `${hours.toString().padStart(2, "0")}:${minutes
      .toString()
      .padStart(2, "0")}:${remainingSeconds.toString().padStart(2, "0")}`
  }

  return (
    <div className="w-full max-w-md mx-auto p-4 bg-white rounded-lg shadow-sm">
      <div className="flex justify-between items-center mb-2">
        <span className="text-sm font-medium">Time Remaining</span>
        <span className="text-lg font-bold">{formatTime(timeLeft)}</span>
      </div>
      <Progress value={progress} className="h-2" />
      {timeLeft <= 300 && ( // Warning when 5 minutes remaining
        <p className="text-red-500 text-sm mt-2 text-center">
          {timeLeft <= 60 ? "Less than 1 minute remaining!" : "5 minutes remaining!"}
        </p>
      )}
    </div>
  )
}
