"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"
import ExamHeader from "@/components/ExamHeader"
import ExamTimer from "@/components/ExamTimer"
import ExamQuestion from "@/components/ExamQuestion"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Alert, AlertDescription } from "@/components/ui/alert"
import type { QuestionType } from "@/components/ExamQuestion"

type SkillType = "reading" | "listening" | "writing" | "speaking"

const MOCK_QUESTIONS = {
  reading: [
    {
      id: "r1",
      type: "multiple-choice" as QuestionType,
      question: "Based on the passage, what is the main idea?",
      passage: `Climate change represents one of the most significant challenges of our time. Recent studies indicate that global temperatures have risen by approximately 1.1°C since pre-industrial times, primarily due to human activities. This warming has led to more frequent extreme weather events, rising sea levels, and disruptions to ecosystems worldwide. While the challenges are significant, there are promising solutions emerging through technological innovation and policy changes. Renewable energy adoption is accelerating, with solar and wind power becoming increasingly cost-competitive with fossil fuels. Additionally, advances in energy storage and smart grid technologies are making clean energy more reliable and accessible.`,
      options: [
        { id: "a", text: "Environmental conservation" },
        { id: "b", text: "Climate change and technological solutions" },
        { id: "c", text: "Economic aspects of renewable energy" },
        { id: "d", text: "Weather pattern analysis" },
      ],
    },
    {
      id: "r2",
      type: "multiple-choice" as QuestionType,
      question: "According to the passage, what is making clean energy more viable?",
      passage: `Climate change represents one of the most significant challenges of our time. Recent studies indicate that global temperatures have risen by approximately 1.1°C since pre-industrial times, primarily due to human activities. This warming has led to more frequent extreme weather events, rising sea levels, and disruptions to ecosystems worldwide. While the challenges are significant, there are promising solutions emerging through technological innovation and policy changes. Renewable energy adoption is accelerating, with solar and wind power becoming increasingly cost-competitive with fossil fuels. Additionally, advances in energy storage and smart grid technologies are making clean energy more reliable and accessible.`,
      options: [
        { id: "a", text: "Government subsidies" },
        { id: "b", text: "Advances in energy storage and smart grid technologies" },
        { id: "c", text: "Public awareness campaigns" },
        { id: "d", text: "International agreements" },
      ],
    },
  ],
  listening: [
    {
      id: "l1",
      type: "listening" as QuestionType,
      question: "What is the main topic of the conversation?",
    },
    {
      id: "l2",
      type: "multiple-choice" as QuestionType,
      question: "What does the speaker suggest as a solution?",
      options: [
        { id: "a", text: "Implementing new policies" },
        { id: "b", text: "Conducting more research" },
        { id: "c", text: "Seeking expert advice" },
        { id: "d", text: "Taking immediate action" },
      ],
    },
  ],
  writing: [
    {
      id: "w1",
      type: "writing" as QuestionType,
      question: "Describe the changes shown in the graph over the past decade.",
    },
    {
      id: "w2",
      type: "writing" as QuestionType,
      question: "Do you agree or disagree with the following statement? Technology has made communication between people less personal. Give reasons for your answer.",
    },
  ],
  speaking: [
    {
      id: "s1",
      type: "speaking" as QuestionType,
      question: "Tell me about your hometown and what you like about it.",
    },
    {
      id: "s2",
      type: "speaking" as QuestionType,
      question: "Describe a situation where you had to make an important decision.",
    },
  ],
}

const SKILL_DURATIONS = {
  reading: 3600, // 60 minutes
  listening: 1800, // 30 minutes
  writing: 3600, // 60 minutes
  speaking: 900, // 15 minutes
}

const SKILL_TITLES = {
  reading: "Reading Test",
  listening: "Listening Test",
  writing: "Writing Test",
  speaking: "Speaking Test",
}

interface SkillExamProps {
  skill: SkillType
}

export default function SkillExam({ skill }: SkillExamProps) {
  const router = useRouter()
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0)
  const [answers, setAnswers] = useState<Record<string, string>>({})
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [error, setError] = useState("")

  const questions = MOCK_QUESTIONS[skill] || []
  const currentQuestion = questions[currentQuestionIndex]

  const handleAnswer = (answer: string) => {
    setAnswers((prev) => ({
      ...prev,
      [currentQuestion.id]: answer,
    }))

    if (currentQuestionIndex < questions.length - 1) {
      setCurrentQuestionIndex((prev) => prev + 1)
    }
  }

  const handleTimeUp = () => {
    handleSubmitExam()
  }

  const handleSubmitExam = async () => {
    setIsSubmitting(true)
    setError("")

    try {
      // In a real app, this would be an API call
      console.log("Submitting answers:", answers)
      
      // Simulate API delay
      await new Promise((resolve) => setTimeout(resolve, 1500))
      
      // Navigate to results page
      router.push("/exam/results")
    } catch (err) {
      setError("Failed to submit exam. Please try again.")
    } finally {
      setIsSubmitting(false)
    }
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <ExamHeader
        title={SKILL_TITLES[skill]}
        subtitle="Complete all questions within the time limit"
      />

      <div className="max-w-4xl mx-auto space-y-6">
        <ExamTimer
          duration={SKILL_DURATIONS[skill]}
          onTimeUp={handleTimeUp}
        />

        {error && (
          <Alert variant="destructive">
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}

        <Card className="p-6">
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-xl font-semibold">
              Question {currentQuestionIndex + 1} of {questions.length}
            </h2>
            <p className="text-gray-600">
              Progress: {Math.round(((currentQuestionIndex + 1) / questions.length) * 100)}%
            </p>
          </div>

          {currentQuestion && (
            <ExamQuestion
              {...currentQuestion}
              onAnswer={handleAnswer}
            />
          )}

          <div className="flex justify-between mt-6">
            <Button
              variant="outline"
              disabled={currentQuestionIndex === 0}
              onClick={() => setCurrentQuestionIndex((prev) => prev - 1)}
            >
              Previous
            </Button>
            {currentQuestionIndex === questions.length - 1 ? (
              <Button
                onClick={handleSubmitExam}
                disabled={isSubmitting}
              >
                {isSubmitting ? "Submitting..." : "Submit Exam"}
              </Button>
            ) : (
              <Button
                disabled={!answers[currentQuestion?.id]}
                onClick={() => setCurrentQuestionIndex((prev) => prev + 1)}
              >
                Next
              </Button>
            )}
          </div>
        </Card>
      </div>
    </div>
  )
}
